.Block_about{
    margin-top: 100px;
    border-top: solid 5px #FFD600;
    padding-bottom: 300px;
    border-bottom: solid 5px #FFD600;
    padding-top: 300px;
    padding-left: 5%;
    padding-right: 5%;
}

.link_about{
    display: flex;
    justify-content: center;
}

.link_about a{
    margin: auto;
    margin-top: 50px;
    color: #FFD600;
    box-shadow: rgba(199, 0, 254, 0.2) 0px 7px 29px 0px;
    padding: 10px;
    border-radius: 10px;
}


.Title_about
{
    display: flex;
    justify-content: center;
    background-color: rgb(0, 0, 0);
    margin-top: 50px;
    border-radius: 10px;
    border-bottom: solid 1px ;
    border-top: solid 1px;
    box-shadow: rgba(0, 0, 0, 0.2) 0px 7px 29px 0px;}

.music2{
    width: 350px;
    /* text-align: center;
    margin: auto;
    display: flex; */
    border-radius: 10px;
    margin-top: 70px;
    margin-right: 30px;
    transition: transform ease-in-out .5s;
}

.music2:hover{
    transform: scale(1.1) rotate(1deg);
}


.About_me p{
    margin-top: 70px;
}



@media screen and (max-width: 830px) {
    .music2 {
        margin-left: 30px;
    }
  }


  .skill{
    padding: 20px;
  }


.block_pic{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    align-items:center;
}

